import { useCallback, useState, useEffect } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'
import { getBoardroom } from 'utils/addressHelpers'
import boardRoom from 'config/abi/boardRoom.json'
import { useBoardroom } from './useContract'

const useHarvestFromBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handle = useCallback(async () => {
    const txHash = await contract.methods.claimReward().send({ from: account })
    return txHash
  }, [account, contract])

  return { onReward: handle }
}

export const useBoardroomState = () => {
  const { account } = useWallet()
  const [balance, setBalance] = useState([])
  const contract = getBoardroom()

  useEffect(() => {
    const fetchAll = async () => {
      const calls = [
        {
          address: contract,
          name: 'balanceOf',
          params: [account],
        },
        {
          address: contract,
          name: 'canWithdraw',
          params: [account],
        },
        {
          address: contract,
          name: 'canClaimReward',
          params: [account],
        },
      ]

      const [balanceOf, canWithdraw, canClaimReward] = await multicall(boardRoom, calls)
      setBalance([new BigNumber(balanceOf).div(new BigNumber(10).pow(18)), canWithdraw[0], canClaimReward[0]])
    }

    if (account) {
      fetchAll()
    }
  }, [account, contract])

  return balance
}

export const useEarningsOnBoardroom = () => {
  const { account } = useWallet()
  const [balance, setBalance] = useState(new BigNumber(0))
  const contract = useBoardroom()

  const fetchBalance = useCallback(async () => {
    setBalance(await contract.methods.earned(account).call())
  }, [account, contract])

  useEffect(() => {
    fetchBalance().catch((err) => console.error(err.stack))

    const refreshBalance = setInterval(fetchBalance, 10000)
    return () => clearInterval(refreshBalance)
  }, [setBalance, fetchBalance])

  return balance
}

export default useHarvestFromBoardroom
